// Page intro
document.addEventListener('alpine:init', () => {
  Alpine.data('admin_list_tagihan', () => ({
    title: 'Daftar Tagihan',
    ui: {
      cacheName: 'admin_list_tagihan',
      loading: false,
      currentData: {},
    },
    data: {},
    model: {},

    init() {
        document.title = this.title;
        Alpine.store('masagi').currentPage = 'profile';

        this._fetchData();
    },

    filteredData() {
      return this.data.list.filter((member) => {
        const searchLower = this.data.searchValue.toLowerCase();
        return (
          member.name.toLowerCase().includes(searchLower) ||
          member.nama_pc.toLowerCase().includes(searchLower)
        );
      });
    },

    _fetchData() {
      this.ui.loading = true;
      if (cachePageData[this.ui.cacheName]?.length > 0) {
        this.data.list = cachePageData[this.ui.cacheName];
      } else {
        fetchPageData('admin/list_tagihan/supply')
        .then((response) => {
            if(response.success == 1) {
                cachePageData[this.ui.cacheName] = response.list;
                this.data.list = response.list;
                this.ui.loading = false;
            }
        });
      }
    },

    setCurrent(index) {
      this.ui.currentData = this.data.list[index]
    }
    
  }));
});
